import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  { path: '/',
    component: require('./components/Portfolio'),
    children: [
      { path: '/', component: require('./components/Foo') },
       { path: 'Graphic', component: require('./components/blogContent') },
       { path: 'Web', component: require('./components/otherblog') },
        { path: 'About', component: require('./components/Bar') }
    ] }
]
const router = new VueRouter({
  mode: 'history',
  base: '/',
  routes: routes,
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
})

module.exports = router



